<template>
<div class="container text-center">
    <img alt="Vue logo" src="../assets/logo.png" class="logo">
    <div class="login-user">
        <input type="email"  v-model="user.email" placeholder="type your email"><br>
        <input type="password" v-model="user.password" placeholder="type your password"><br>
        <button @click="load_user" class="btn btn-primary">Login</button>
    </div>
</div>
</template>

<script>
import users from '../auth';

export default {
    name: 'login',
    data() {
        return {
            user: {
                email: '',
                password: ''
            }
        }
    },
    methods: {
        async load_user() {
            await users.load_user(this.user.email, this.user.password);
            this.users = await users.get_users();
            alert('voce esta logado!')
        }
    }
}
</script>


<style scoped>
button {
    margin: 10px;
}
input {
    width: 30%;
    padding: 7px;
    border: solid 1px blue;
    border-radius: 5px;
    margin: 5px;
}
.login-user {
    margin-top: 3%;
    padding: 10px 5px 10px 5px;
}
.logo {
    margin-top: 5%;
}
</style>