import Vue from 'vue'
import Router from 'vue-router'

import register from '@/components/register'
import post from '@/components/PostComponent'
import login from '@/components/login'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '*',
      redirect: '/login'
    },
    {
    path: '/register',
    component: register
    },
    {
    path: '/login',
    component: login
    },
    {
    path: '/',
    component: post
    }
  ]
})

export default router;