import axios from 'axios';

const url = 'api/users/';
const url_register = 'api/users/register';
const url_login = 'api/users/login';

class users {
    static get_users() {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(url);
                const data = res.data;
                resolve(data.map(user => ({ ...user })))
            } catch(err) {
                reject(err);
            }
        });
    }
    static new_user (email, password) {
        return axios.post(url_register, {
            email, password
        });
    }

    static load_user (email, password) {
        return axios.post(url_login, {
            email, password
        });
    }
}

export default users;