<template>

<div class="container text-center">
    <img alt="Vue logo" src="../assets/logo.png" class="logo">
    <div class="new-user">
        <input type="email" v-model="email" placeholder="Type your email"><br>
        <input type="password" v-model="password" placeholder="Type your password"><br>
        <button @click="create_user" class="btn btn-primary">Sing-up</button>
    </div>
</div>
</template>

<script>
import users from '../auth'

export default {
    data() {
        return {
            users: [],
            email: '',
            password: '',
            error: ''
        }
    },
    methods: {
        async create_user() {
            await users.new_user(this.email, this.password);
            this.users = await users.get_users();
        }
    }
}
</script>

<style scoped>
button {
    margin: 10px;
}
input {
    width: 30%;
    padding: 7px;
    border: solid 1px blue;
    border-radius: 5px;
    margin: 5px;
}
.new-user {
    margin-top: 3%;
    padding: 10px 5px 10px 5px;
}
.logo {
    margin-top: 5%;
}
</style>